@import "variables";
@import "mixins";

.page-header {
  .menu {
    a {
      padding:0 50px;
    }
    li {
      &.work a {
        padding-right:50px;
      }
      &.blog a {
        padding-right:50px;
      }
    }
  }
}

// Blog
#pane-blog {
  #block-views-block-blog-latest-posts-block-1,
  #block-views-block-blog-view-a-topic-block-1 {
    form.views-exposed-form {
      margin-bottom: 13px;
    }
    .views-row {
      width: 33.333%;
      box-sizing: border-box;
      margin: 0;
      padding: 1.35%;
      &.views-row:last-child {
        display: block;
      }
    }
  }

  .block {
    margin: 0 30px;
    max-width: 100%;
  }
}

.node--type-portfolio {
  &.node--view-mode-teaser {
    position: relative;
    font-size: 25px;
    .field--name-field-cover-image:before {
      opacity: 0;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 6px;
      content: ' ';
      transition: opacity 0.25s;
      @include lastcall-grey-overlay-bg();
      box-shadow: 0 0 0 5px #fff inset;
    }
    &:hover {
      h2 {
        opacity: 1;
        a {
          color: #fff;
          border-top: 1px solid #fff;
          border-bottom: 1px solid #fff;
        }
      }
      .field--name-field-cover-image:before {
        opacity: 1;
      }
      .portfolio-button {
        opacity: 1;
        color: $body_color;
        > span {
          background: #fff;
        }
      }
    }

    h2 {
      transition: opacity 0.25s;
      opacity: 0;
      position: absolute;
      width: 100%;
      max-width: 100%;
      text-align: center;
      top: 20px;
      a {
        padding: 0 10px;
        margin-top: 20px;
        color: transparent;
      }
    }
    a.modal-control-link {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 2;
    }
    .portfolio-button {
      transition: opacity 0.25s;
      color: transparent;
      opacity: 0;
      position: absolute;
      left: 50%;
      top: 70px;
      width: 0px;
      padding: 0;

      > span {
        width: 125px;
        height: 125px;
        border-radius: 50%;
        background: transparent;
        display: block;
        margin-left: -62px;
        position: relative;
        > span {
          position: absolute;
          width: 100px;
          left: 12px;
          overflow: visible;
          text-align: center;
          top: 25%;
        }
      }
    }
  }
}

#block-portfoliologos {
  position: relative;
  z-index: 0;
  #container {
    display: table;
    width: 100%;
  }
  h2 {
    font-family: $pathway_font_stack;
    font-weight: normal;
    margin: 10px;
    font-size: 36px;
    text-transform: uppercase;
  }
  .portfolio-logos-slideshow {
    display: table-cell;
    width: 20%;
    height: 65px;
    img {
      width: auto;
      max-width: 160px;
      height: auto;
      max-height: 65px;
    }
  }
}
