// Mixins we can use elsewhere

@mixin list-no-style {
  &, & li {
    list-style: none;
    margin-left:0;
    padding-left:0;
  }
}

@mixin background-image-retina($file, $extension, $width, $height) {
  background-image: url($file + '.' + $extension);
  @media (-webkit-min-device-pixel-ratio: 2), (-moz-min-device-pixel-ratio: 2) {
    & {
      background-image: url($file + '@2x.' + $extension);
      -webkit-background-size: $width $height;
    }
  }
}

@mixin lastcall-grey-overlay-bg() {
  background: rgba(63, 68, 79, .85);
}

@mixin placeholder {
  &::-moz-placeholder { @content }
  &::-webkit-input-placeholder { @content }
  &:-ms-input-placeholder { @content }
}


//
// Helps creating links that have a "slot machine" hover effect.
//

@mixin slot-links-container($height) {
  height:   $height;
  line-height: $height;
  overflow: hidden;
}

@mixin slot-link($height, $element: 'a', $normal: '&::before', $active: '&::after') {
  #{$element} {
    transition: margin-top 0.25s ease 0s;
    display:     block;
    font-family: 'fontello';
    height:      $height * 2;
    &:hover {
      margin: -$height 0 0 0;
    }
    #{$normal} {
      color:   $body_color;
      display: block;
      height:  $height;
    }
    #{$active} {
      color:   $lastcall_red;
      display: block;
      height:  $height;
    }
  }
}
